import logo from '../resources/logo.svg';
import '../App.css';
import Button from 'react-bootstrap/Button';
import {useHistory} from "react-router-dom";

import {Link} from "react-router-dom";

import Form from 'react-bootstrap/Form';

import React, {useState, useEffect} from 'react';
import { sendauthenticationDataHelper } from "../services/restHandler";

import {createAuthCookie} from '../auth'
import { isAuthenticated } from '../auth';

function App(props) {

    let history = useHistory()

    const onFormSubmit = e => {
        e.preventDefault()
        var userbody = {
            user : e.target.formBasicEmail.value,
            password : e.target.formBasicPassword.value,
        }
        sendauthenticationDataHelper(userbody).then((res) => {
            if (res == "Falsche Anmeldedaten") {
                window.alert("Bitte geben Sie Ihre korrekten Anmeldedaten ein")
                return
            }
            console.log(res)
            createAuthCookie(123); // remove later. not for production
            history.push("/dashboard")
        })
    }

    let loginBoxPosition = {
        "width": "400px",
        "top": "50%",
        "left": "50%",
        "transform": "translate(-50%, -50%)",
        "position": "absolute"
    }

    if (isAuthenticated()) {
    //if (true){
        window.location.replace('/dashboard');
        return;
    }

     return (
        <div className="App" style={{"background":"#23272A", "height":"100vh"}}>
            <div style={loginBoxPosition}>
              <div style={{"color":"#3C8761", "font-size":"6em"}}>Login</div>
              <div style={{"color":"white", "font-size":"1.0em","margin-bottom":"20px"}}>
                  <h6>Melden Sie sich an, um Zugang zu Ihrem individuellen Stromverbrauch zu erhalten.</h6> </div>
                <Form onSubmit={onFormSubmit}>
                    <Form.Group controlId="formBasicEmail">
                        <Form.Control type="text" placeholder="Geben Sie hier Ihren Username ein"/>
                    </Form.Group>
                    <Form.Group controlId="formBasicPassword">
                        <Form.Control type="password" placeholder="Geben Sie hier Ihr Passwort ein"/>
                    </Form.Group>
                    <Button variant="primary" type="submit">
                        Anmelden
                    </Button>
                </Form>
            </div>
        </div>
    ); 
}

export default App;
