export function getConsumptionDataHelper() {
    var options = {
        method: 'GET',
        uri: baseUri + '/consumptionData'
    }
    return rp(options)
}

export function getCompilationDataHelper(numberOfTxnCompilation) {
    var options = {
        method: 'GET',
        uri: baseUri + '/compilationData',
        qs: {
            numberOfTxnCompilation: numberOfTxnCompilation
        }
    }
    return rp(options)
}

export function getCompilationAverageDataHelper() {
    var options = {
        method: 'GET',
        uri: baseUri + '/averageCompilationData'
    }
    return rp(options)
}

// Funktion, um "selbst" generierte Emissionsdaten aus der nedb zu ziehen
export function getEmissionDataHelper(numberOfTxn) {
    var options = {
        method: 'GET',
        uri: baseUri + '/emissionData',
        qs: {
            numberOfTxn: numberOfTxn
        }
    }
    return rp(options)
}
// Funktion, um Daten aus der forecast.db von der API ("/forecastData"), wo die Daten von "getForecastData" zur Verf체gung gestellt werden, zu ziehen
export function getForecastDataHelper() {
    var options = {
        method: 'GET',
        uri: baseUri + '/forecastData',
    }
    return rp(options)
}

export function getFootprintDataDataHelper() {
    var options = {
        method: 'GET',
        uri: baseUri + '/footprintData'
    }
    return rp(options)
}

export function sendPriorizationDataHelper(priorizationValues) {
    var options = {
        method: 'POST',
        uri: baseUri + '/priorizationData',
        body: priorizationValues,
        json: true
    }
    return rp(options)
}

export function queryPriorizationDataHelper(priorizationValues) {
    var options = {
        method: 'GET',
        uri: baseUri + '/priorizationData'
    }
    return rp(options)
}

