import Button from "react-bootstrap/Button";
import Container from "react-bootstrap/Container";
import Row from "react-bootstrap/Row";
import Col from "react-bootstrap/Col";
import { Card } from "react-bootstrap";
import "./priorization.css";
import priorisierungsskala from "../../../resources/PrioSkala.png";
import windkraftIcon from "../../../resources/windkraftlogo.png";
import wasserkraftIcon from "../../../resources/wasserkraftlogo.png";
import photovoltaikIcon from "../../../resources/photovoltaiklogo.png";
import biogasIcon from "../../../resources/biogaslogo.svg";
import geothermieIcon from "../../../resources/geothermielogo.svg";
import { WindMillLoading } from 'react-loadingg';

import ReactBootstrapSlider from "react-bootstrap-slider";
import { useState, useEffect } from "react";
import {
  sendPriorizationDataHelper,
  queryPriorizationDataHelper,
} from "../../../services/restHandler";

function App() {

  var initialValue = {
    wind: 0,
    wasser: 0,
    photovoltaik: 0,
    biogas: 0,
    geothermie: 0,
  };

  const [timestamp, setTimestamp] = useState();
  const [prioValueWind, setPrioValueWind] = useState(initialValue.wind);
  const [prioValueWasser, setPrioValueWasser] = useState(initialValue.wasser);
  const [prioValuePhotovoltaik, setPrioValuePhotovoltaik] = useState(
    initialValue.photovoltaik
  );
  const [prioValueBiogas, setPrioValueBiogas] = useState(initialValue.biogas);
  const [prioValueGeothermie, setPrioValueGeothermie] = useState(
    initialValue.geothermie
  );

  const [dataLoaded, setDataLoaded] = useState(false);

  useEffect(function () {
    queryPriorizationDataHelper().then(function (result) {
      if (result != "") {
        let queriedPriorizationData = JSON.parse(result);
        setTimestamp(queriedPriorizationData.timestamp);
        setPrioValueWind(queriedPriorizationData.prioValueWind);
        setPrioValueWasser(queriedPriorizationData.prioValueWasser);
        setPrioValuePhotovoltaik(queriedPriorizationData.prioValuePhotovoltaik);
        setPrioValueBiogas(queriedPriorizationData.prioValueBiogas);
        setPrioValueGeothermie(queriedPriorizationData.prioValueGeothermie);
        setDataLoaded(true);
      } else {
        setTimestamp(0);
        setPrioValueWind(1);
        setPrioValueWasser(1);
        setPrioValuePhotovoltaik(1);
        setPrioValueBiogas(1);
        setPrioValueGeothermie(1);
        setDataLoaded(true);
      }
      console.log(result);
      setDataLoaded(true);
    });
  }, []);

  function sendButton() {
    let priorizationValues = {
      timestamp: Date.now(),
      prioValueWind: prioValueWind,
      prioValueWasser: prioValueWasser,
      prioValuePhotovoltaik: prioValuePhotovoltaik,
      prioValueBiogas: prioValueBiogas,
      prioValueGeothermie: prioValueGeothermie,
    };
    if (timestamp == undefined) { //falls priorization DB leer ist, dann wird hier ein initialer Eintrag erzeugt
      sendPriorizationDataHelper(priorizationValues).then(function () {
        window.alert("Sie haben Ihre erste Priorisierung erfolgreich gesendet");
      });
      console.log(priorizationValues);
    }
    if (timestamp < Date.now() - 86400000) {
      sendPriorizationDataHelper(priorizationValues).then(function () {
        window.alert("Priorisierung erfolgreich aktualisiert");
      });
      console.log(priorizationValues);
    } 
    else {
      console.log(timestamp);
      window.alert("Sie k철nnen Ihren Strommix erst wieder in " + Math.floor((86400000 - Date.now() + timestamp )/3600000)+ " Stunden und " + Math.ceil(((86400000 - Date.now() + timestamp )%3600000)/60000)  + " Minuten 채ndern.");
    }
  }

  return (
    <div className="App" style={{ maxWidth: "1200px" }}>
      <div>
        {dataLoaded ? (
          <Container
          style={{ paddingLeft: "40px", paddingTop: "20px", margin: "unset" }}>
            <Card>
              <Card.Title
                className="text-left"
                style={{ color: "#3C8761", margin: "10px" }}
              >
                Priorisierung des Gr체nstromanteils
              </Card.Title>
              <Card.Body className="text-center" style={{ margin: "40px" }}>
                <Row
                  xs={6}
                  sm={6}
                  md={6}
                  lg={6}
                  xl={6}
                  style={{ fontSize: "16px"}}
                  className={"prioSliders"}
                >
                  <Col style={{ fontSize: "14px",  marginBottom : "25px" }}>
                    Prio: {prioValueWind}
                  </Col>
                  <Col style={{ fontSize: "14px" }}>
                    Prio: {prioValueWasser}
                  </Col>
                  <Col style={{ fontSize: "14px" }}>
                    Prio: {prioValuePhotovoltaik}
                  </Col>
                  <Col style={{ fontSize: "14px" }}>
                    Prio: {prioValueBiogas}
                  </Col>
                  <Col style={{ fontSize: "14px" }}>
                    Prio: {prioValueGeothermie}
                  </Col>
                  <Col>
                    <div
                      style={{
                        textAlign: "left",
                        fontSize: "10px"
                      }}
                    >
                      {" "}
                      Hohe Priorisierung
                    </div>
                  </Col>
                  <Col
                    style={{
                      marginBottom: "22px",
                      borderBlockEndStyle: "solid",
                      borderBlockEndWidth: "thin",
                    }}
                  >
                    <ReactBootstrapSlider
                      id="wind"
                      value={prioValueWind}
                      change={(event) => setPrioValueWind(event.target.value)}
                      step={1}
                      max={5}
                      min={1}
                      orientation="vertical"
                      reversed={false}
                    />
                  </Col>
                  <Col
                    style={{
                      marginBottom: "22px",
                      borderBlockEndStyle: "solid",
                      borderBlockEndWidth: "thin",
                    }}
                  >
                    <ReactBootstrapSlider
                      style={{ height: "185px", top: "15px" }}
                      id="wasser"
                      value={prioValueWasser}
                      change={(event) => setPrioValueWasser(event.target.value)}
                      step={1}
                      max={5}
                      min={1}
                      orientation="vertical"
                      reversed={false}
                    />
                  </Col>
                  <Col
                    style={{
                      marginBottom: "22px",
                      borderBlockEndStyle: "solid",
                      borderBlockEndWidth: "thin",
                    }}
                  >
                    <ReactBootstrapSlider
                      id="photovoltaik"
                      value={prioValuePhotovoltaik}
                      change={(event) =>
                        setPrioValuePhotovoltaik(event.target.value)
                      }
                      step={1}
                      step={1}
                      max={5}
                      min={1}
                      orientation="vertical"
                      reversed={false}
                    />
                  </Col>
                  <Col
                    style={{
                      marginBottom: "22px",
                      borderBlockEndStyle: "solid",
                      borderBlockEndWidth: "thin",
                    }}
                  >
                    <ReactBootstrapSlider
                      id="biogas"
                      value={prioValueBiogas}
                      change={(event) => setPrioValueBiogas(event.target.value)}
                      step={1}
                      max={5}
                      min={1}
                      orientation="vertical"
                      reversed={false}
                    />
                  </Col>
                  <Col
                    style={{
                      marginBottom: "22px",
                      borderBlockEndStyle: "solid",
                      borderBlockEndWidth: "thin",
                    }}
                  >
                    <ReactBootstrapSlider
                      id="geothermie"
                      value={prioValueGeothermie}
                      change={(event) =>
                        setPrioValueGeothermie(event.target.value)
                      }
                      step={1}
                      max={5}
                      min={1}
                      orientation="vertical"
                      reversed={false}
                    />
                  </Col>
                  <Col>
                    <div>
                      <img
                        style={{ float: "left", height: "227px" }}
                        src={priorisierungsskala}
                      />
                    </div>
                  </Col>
                  <Col>
                    {" "}
                    <img src={windkraftIcon} />{" "}
                    <div style={{ fontSize: "12px" }}>Windkraft</div>
                  </Col>
                  <Col>
                    {" "}
                    <img src={wasserkraftIcon} />{" "}
                    <div style={{ fontSize: "12px" }}>Wasserkraft</div>
                  </Col>
                  <Col>
                    {" "}
                    <img src={photovoltaikIcon} />{" "}
                    <div style={{ fontSize: "12px" }}>Photovoltaik</div>
                  </Col>
                  <Col>
                    {" "}
                    <img style={{ height: "53px" }} src={biogasIcon} />{" "}
                    <div style={{ fontSize: "12px" }}>Biomasse</div>
                  </Col>
                  <Col>
                    {" "}
                    <img style={{ height: "53px" }} src={geothermieIcon} />{" "}
                    <div style={{ fontSize: "12px" }}>Geothermie</div>
                  </Col>
                  <Col>
                    <div
                      style={{
                        textAlign: "left",
                        fontSize: "10px"
                      }}
                    >
                      {" "}
                      Niedrige Priorisierung
                    </div>
                    <div style={{ marginTop: "40px" }}>
                      <Button
                        className="text-center"
                        onClick={sendButton}
                        style={{
                          height: "60px",
                          width: "130px",
                          textAlign: "center",
                          backgroundColor: "#D5EADB",
                          fontSize: "14px",
                          fontWeight: "bold",
                          fontFamily: "Avenir Next (Medium)",
                          color: "lightgray",
                          borderColor : "#23272A"
                        }}
                      >
                        <div style={{ textAlign: "center", color: "#23272A" }}>
                          Gr체nstrommix Absenden
                        </div>
                      </Button>
                    </div>
                  </Col>
                </Row>
              </Card.Body>
            </Card>
          </Container>
        ) : (
          <div> <WindMillLoading size = "large" color = "#23272A" /></div>
        )}
      </div>
    </div>
  );
}

export default App;
